.footer {
    background: #928d93;
    height: 100px;
    position: relative;
    color: #fff;
    overflow: auto;
}

.footer>.bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #557A95;
    padding: 8px;
    font-weight: 600;
}

.footer>.bottom>a {
    width: 5em;
    position: relative;
    top: 3px;
    left: 2px;
    color:  white;
}






